    .card {
    position: relative;
    margin: 7px 7px;
    float: left;
    /* width: 220px; */
    width: 18%;
    height: 200px;
  box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.3);
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  
  .card_title {
    font-weight: bold;
    font-family: 'Kurale', serif;
    /* font-family: 'Alegreya', serif; */
    overflow-y: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 15px;
    margin: 5px;
    height: 40px;
    color: #686868;
  }

  .card_date{
    text-align: center;
    font-size: 12px;
    margin: 5px;
    color: #686868;
  }

  .card_group{
    text-align: center;
    font-size: 12px;
    color: #686868;
  }

  .card_description {
    overflow-y: scroll;
    font-family: 'Alegreya', serif;
    text-align: center;
    font-weight: bold;
    font-size: 14px;
    margin: 5px;
    height: 65px;
    color: #686868;
  }

  .card_price {
    position: absolute;
    bottom:0;
    left:0;
    color: #bd0909 !important;
    padding: 5px;
    margin-left: 5px;
    display: inline-block;
    /* transition: all 0.4s ease 0s; */
    }

  .card_editButton {
    position: absolute;
    bottom:0;
    right:30px;
    color: #686868 !important;
    padding: 5px;
    display: inline-block;
    -webkit-transition: all 0.4s ease 0s;
    transition: all 0.4s ease 0s;
    }

    .card_editButton:hover {
      color: #1d8c07 !important;
      -webkit-transition: all 0.4s ease 0s;
      transition: all 0.4s ease 0s;
      }

  .card_removeButton {
    position: absolute;
    bottom:0;
    right:0;
    color: #686868 !important;
    padding: 5px;
    display: inline-block;
    -webkit-transition: all 0.4s ease 0s;
    transition: all 0.4s ease 0s;
    }

    .card_removeButton:hover {
      color: #a90f0f !important;
      -webkit-transition: all 0.4s ease 0s;
      transition: all 0.4s ease 0s;
      }
  
  .card:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    box-shadow: 0 30px 60px 0 rgba(0, 0, 0, 0.3);
  }
  
  .title-black {
    color: #da1010;
  }

  @media (max-width: 60em) {
    .card {
      width: 100%;
    }
  }

  @media all and (max-width: 500px) {
    .card-list {
      flex-direction: column;
    }
  }
